import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";

const useStyles = makeStyles({
  table: {
    minWidth: 380,
  },
});

function createData(name, phone, group, link) {
  return { name, phone, group, link };
}

const rows = [
  createData(" ", "0973055477", "1", " tel:0973055477"),
  createData(" ", "0939028646", "1", " tel:0939028646"),

  createData(" ", "0963802787", "2", "tel:0963802787"),
  createData(" ", "0939333633", "2", "tel:0939333633"),

  createData(" ", "0984988849", "3", "tel:0984988849"),
  createData(" ", "0965677644", "3", "tel:0965677644"),

  createData(" ", "0635609455", "4", " tel:0635609455"),
  createData(" ", "0632007996", "4", " tel:0632007996"),

  createData(" ", "0972108360", "5", " tel:0972108360"),
  createData(" ", "0980085403", "5", " tel:0980085403"),
];

export default function Responsible() {
  const classes = useStyles();

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell>'</TableCell>
            <TableCell align="center"></TableCell>
            <TableCell align="right"></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.name}>
              <TableCell component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell align="center">{row.group}</TableCell>
              <TableCell align="right">
                <a href={row.link}>{row.phone}</a>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
